import React, { Component } from 'react';

class RefsTest extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.submit = this.submit.bind(this);
    this.onTextFieldFocus = this.onTextFieldFocus.bind(this);
  }

  submit() {
    console.log("submit",this.testInput.value);
  };

  onTextFieldFocus() {
    console.log("click on input");
  }

  componentDidMount() {
    this.testInput.focus();
  }

  render() {
    return (
      <div className="container">
        <input
          type="text" placeholder="test"
          ref={(input)=>this.testInput = input}
          onClick={this.onTextFieldFocus}
        />
        <button onClick={this.submit}>Submit</button>
      </div>
    );
  }
}

export default RefsTest;



